.loading-animation-container {
	display: grid;
	grid-template-columns: repeat(5, 10px);
	grid-auto-rows: 10px;
	gap: 10px;
}

.animation-grid-item {
	animation: jumping 1.25s infinite ease-in-out;
	border-radius: 25px;
}

.animation-grid-item:nth-of-type(1) {
	background-color: hsl(231, 65%, 35%);
	animation-delay: 0;
}

.animation-grid-item:nth-of-type(2) {
	background-color: hsl(231, 65%, 35%);
	animation-delay: 0.2s;
}

.animation-grid-item:nth-of-type(3) {
	background-color: hsl(231, 65%, 35%);
	animation-delay: 0.4s;
}

.animation-grid-item:nth-of-type(4) {
	background-color: hsl(231, 65%, 35%);
	animation-delay: 0.6s;
}

.animation-grid-item:nth-of-type(5) {
	background-color: hsl(231, 65%, 35%);
	animation-delay: 0.8s;
}

@keyframes jumping {
	0% {
		transform: translateY(0);
	}
	50% {
		transform: translateY(-15px) scale(1.5, 1.5);
		background-color: hsl(231, 65%, 51%);
	}
	100% {
		transform: translateY(0);
	}
}
