.product-list {
  position: relative;
  width: 100%;
  height: 100%;
  padding: 46px 0px 0px;
}

.product-list__p {
  margin-left: 24px;
  margin-bottom: 23px;
  font-size: 1.4rem;
  color: #b9c1cc;
}

.product-list-props {
  height: 85%;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  justify-items: center;
  position: relative;
  overflow: scroll;
  column-gap: 0px;
  row-gap: 20px;
}
.product-list-props::-webkit-scrollbar {
  width: 0;
}
